.section{
    margin: 0;
    padding: 60px 0;
    color: white;
    background-color: deepskyblue;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.section h1{
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 18px;
}

.section p{
    font-style: italic;
    margin-bottom: 18px;
}

.setion-center{
    width: 100%;
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
}

.section-img{
    width: 700px;
    margin: 0;
    padding: 0;
    margin-top: 20px;
}

.section img{
    width: 700px;
}

.styled-container{
    margin: 0 -2rem;
    padding: 0 2rem;
}

.deadline{
    margin-top: 12px;
}

@media(max-width: 350px){
    .section img{
        width: 90%;
    }
}