.main{
    margin: 100px 0;
    display: flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form{
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    width: 800px;
}

.form .input-group{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 18px 0;
}

.form button{
    background-color: #3c0d99;
    color: #fff;
    padding: 12px 24px;
    border-radius: 4px;
}

.form input{
    border-bottom: 1px solid #000;
    padding-bottom: 6px;
    font-size: 1.1rem;
    text-align: left !important;
    flex: 1;
    margin: 0 12px;
}

.form input:focus{
    outline: none;
}

.main-content{
    width: 800px;
}
.main-content a:link{
    color: (internal value);
    text-decoration: underline;
    cursor: auto;	
}
.main-content a:visited{
    color: (internal value);
    text-decoration: underline;
    cursor: auto;	
}
.main-content a:link:active{
    color: (internal value);
}
.main-content a:visited:active{
    color: (internal value);
}	
.main-content address{	
    display: block;
    font-style: italic;
}	
.main-content area{
    display: none;
}	
.main-content article{
    display: block;
}
.main-content aside{
    display: block;
}	
.main-content b{
    font-weight: bold;
}	
.main-content bdo{
    unicode-bidi: bidi-override;
}	
.main-content blockquote{
    display: block;
    margin-top: 1em;
    margin-bottom: 1em;
    margin-left: 40px;
    margin-right: 40px;
}
.main-content body{
    display: block;
    margin: 8px;
}	
.main-content body:focus{
    outline: none;
}	
.main-content caption{
    display: table-caption;
    text-align: center;	
}
.main-content cite{
    font-style: italic;
}	
.main-content code{
    font-family: monospace;
}	
.main-content col{
    display: table-column;
}	
.main-content colgroup{
    display: table-column-group;
}	
.main-content datalist{
    display: none;
}	
.main-content dd{
    display: block;
    margin-left: 40px;
}	
.main-content del{
    text-decoration: line-through;
}	
.main-content details{
    display: block;
}
.main-content dfn{
    font-style: italic;	
}
.main-content div{
    display: block;	
}
.main-content dl{
    display: block;
    margin-top: 1em;
    margin-bottom: 1em;
    margin-left: 0;
    margin-right: 0;	
}
.main-content dt{
    display: block;
}	
.main-content em{
    font-style: italic;
}	
.main-content embed:focus{
    outline: none;
}	
.main-content fieldset{
    display: block;
    margin-left: 2px;
    margin-right: 2px;
    padding-top: 0.35em;
    padding-bottom: 0.625em;
    padding-left: 0.75em;
    padding-right: 0.75em;
    border: 2px groove (internal value);
}
.main-content figcaption{
    display: block;
}	
.main-content figure{
    display: block;
    margin-top: 1em;
    margin-bottom: 1em;
    margin-left: 40px;
    margin-right: 40px;
}	
.main-content footer{
    display: block;
}	
.main-content form{
    display: block;
    margin-top: 0em;
}
.main-content h1{
    display: block;
    font-size: 2em;
    margin-top: 0.67em;
    margin-bottom: 0.67em;
    margin-left: 0;
    margin-right: 0;
    font-weight: bold;	
}
.main-content h2{
    display: block;
    font-size: 1.5em;
    margin-top: 0.83em;
    margin-bottom: 0.83em;
    margin-left: 0;
    margin-right: 0;
    font-weight: bold;
}
.main-content h3{
    display: block;
    font-size: 1.17em;
    margin-top: 1em;
    margin-bottom: 1em;
    margin-left: 0;
    margin-right: 0;
    font-weight: bold;	
}
.main-content h4{
    display: block;
    margin-top: 1.33em;
    margin-bottom: 1.33em;
    margin-left: 0;
    margin-right: 0;
    font-weight: bold;
}
.main-content h5{
    display: block;
    font-size: .83em;
    margin-top: 1.67em;
    margin-bottom: 1.67em;
    margin-left: 0;
    margin-right: 0;
    font-weight: bold;
}
.main-content h6{
    display: block;
    font-size: .67em;
    margin-top: 2.33em;
    margin-bottom: 2.33em;
    margin-left: 0;
    margin-right: 0;
    font-weight: bold;
}
.main-content head{
    display: none;	
}
.main-content header{
    display: block;	
}
.main-content hr{
    display: block;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
    margin-left: auto;
    margin-right: auto;
    border-style: inset;
    border-width: 1px;	
}
.main-content html{
    display: block;	
}
.main-content html:focus{
    outline: none;	
}
.main-content i{
    font-style: italic;	
}
.main-content iframe:focus{
    outline: none;	
}
.main-content iframe[seamless]{
    display: block;	
}
.main-content img{
    display: inline-block;	
}
.main-content ins{
    text-decoration: underline;	
}
.main-content kbd{
    font-family: monospace;	
}
.main-content label{
    cursor: default;	
}
.main-content legend{
    display: block;
    padding-left: 2px;
    padding-right: 2px;
    border: none;	
}
.main-content li{
    display: list-item;	
}
.main-content link{
    display: none;	
}
.main-content map{
    display: inline;
}	
.main-content mark{
    background-color: yellow;
    color: black;	
}
.main-content menu{
    display: block;
    list-style-type: disc;
    margin-top: 1em;
    margin-bottom: 1em;
    margin-left: 0;
    margin-right: 0;
    padding-left: 40px;	
}
.main-content nav{
    display: block;	
}
.main-content object:focus{
    outline: none;	
}
.main-content ol{
    display: block;
    list-style-type: decimal;
    margin-top: 1em;
    margin-bottom: 1em;
    margin-left: 0;
    margin-right: 0;
    padding-left: 40px;	
}
.main-content output{
    display: inline;	
}
.main-content p{
    display: block;
    margin-top: 1em;
    margin-bottom: 1em;
    margin-left: 0;
    margin-right: 0;	
}
.main-content param{
    display: none;
}	
.main-content pre{
    display: block;
    font-family: monospace;
    white-space: pre;
    margin: 1em 0;	
}
.main-content q{
    display: inline;
}	
.main-content q::before{
    content: open-quote;
}	
.main-content q::after{
    content: close-quote;
}		
.main-content rt{
    line-height: normal;
}	
.main-content s{
    text-decoration: line-through;
}	
.main-content samp{
    font-family: monospace;
}	
.main-content script{
    display: none;
}	
.main-content section{
    display: block;
}		
.main-content small{
    font-size: smaller;
}		
.main-content strike{
    text-decoration: line-through;
}	
.main-content strong{
    font-weight: bold;
}	
.main-content style{
    display: none;
}	
.main-content sub{
    vertical-align: sub;
    font-size: smaller;
}	
.main-content summary{
    display: block;
}	
.main-content sup{
    vertical-align: super;
    font-size: smaller;	
}
.main-content table{
    display: table;
    border-collapse: separate;
    border-spacing: 2px;
    border-color: gray;	
}
.main-content tbody{
    display: table-row-group;
    vertical-align: middle;
    border-color: inherit;	
}
.main-content td{
    display: table-cell;
    vertical-align: inherit;	
}
.main-content tfoot{
    display: table-footer-group;
    vertical-align: middle;
    border-color: inherit;	
}
.main-content th{
    display: table-cell;
    vertical-align: inherit;
    font-weight: bold;
    text-align: center;	
}
.main-content thead{
    display: table-header-group;
    vertical-align: middle;
    border-color: inherit;	
}
.main-content title{
    display: none;	
}
.main-content tr{
    display: table-row;
    vertical-align: inherit;
    border-color: inherit;	
}
.main-content u{
    text-decoration: underline;	
}
.main-content ul{
    display: block;
    list-style-type: disc;
    margin-top: 1em;
    margin-bottom: 1 em;
    margin-left: 0;
    margin-right: 0;
    padding-left: 40px;	
}
.main-content var{
    font-style: italic;	
}