.header1{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	text-align: center;
	justify-content: center;
	align-items: center;
	background-color: #000;
	color: #fff;
	font-size: 1.2rem;
	z-index: 100;
	box-sizing: border-box;
	width: 100%;
	padding: 10px;
	font-weight: bold;
}

.header2{
	background-color: #3C0D99;
}
.header2 p, .header1 p{
	margin-bottom: 0;
}

.header1 button{
	margin-left: 24px;
}

.logo{
	min-width: 150px !important;
	cursor: pointer;
}