.TabCardGrid__HeaderRow-d3lyjf-0{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

*{
    text-decoration: none !important;
    text-align: center !important;
}

.productPrice{
    margin-top: 5px;
    display: flex;
    flex-direction: column;
}

.productDe, .productNum{
    font-size: .7rem;
}

.productPor{
    font-size: 1.5rem;
    font-weight: bold;
}

.pagination{
    margin-top: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.pagination a, .pagination span{
    padding: 4px 12px;
    font-size: 1rem;
    border: 1px solid #fff;
}
a.previous{
    border-radius: 4px 0 0 4px;
    border-right: none;
}
a.next{
    border-radius: 0 4px 4px 0;
    border-left: none;
}

/* tab-control */
.tab-control{
    display: flex;
    align-items: center;
    font-size: 1rem;
    cursor: pointer;
    padding: 12px;
    color: #718096;
    transition: all 0.15s linear;
}
.tab-control:not(:last-of-type){
    margin-right: 6px;
}
.tab-control:hover{
    filter: brightness(1.1);
}
.product-title{
    font-weight: semibold;
    font-size: 1.1rem;
}