#positives{
    margin: 48px 0;
}

.positives-cards{
    margin-top: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.positives-card{
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 20%;
}

.positives-card:not(:last-of-type){
    margin-right: 96px;
}

.positives-card img{
    border-radius: 4px;
}

.positives-card .positives-title, .positives-card .positives-subtitle{
    margin-top: 12px;
    font-size: 1.1rem;
    font-weight: bold;
}

.positives-card .positives-subtitle{
    color: #7c8ba1;
}

@media(max-width: 1120px){
    .positives-cards{
        flex-direction: column;
    }
    .positives-card:not(:last-of-type){
        margin-right: 0;
        margin-bottom: 24px;
    }
}