.section-secondary{
    padding: 24px 0;
    background-color: #fff;
    color: #2b2b2b;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 0.8rem;
}

.section-secondary img{
    max-width: 125px;
}

.section-secondary p{
    margin-top: 24px;
}

.section-secondary img{
    margin-bottom: 24px;
}

.section-secondary div div, .section-secondary div div p{
    display: inline;
    margin-bottom: 0;
}

.section-secondary div span{
    margin-right: 6px;
}